@import url('./__wrapper/profile__wrapper.css');
@import url('./__avatar/profile__avatar.css');
@import url('./__avatar-edit/profile__avatar-edit.css');
@import url('./__profile-info/profile__profile-info.css');
@import url('./__full-name/profile__full-name.css');
@import url('./__edit-button/profile__edit-button.css');
