.profile {
  display: flex;
  align-items: center;
  max-width: 882px;
  margin: 40px auto 0;
}

@media screen and (max-width: 900px) {
  .profile {
    margin: 40px 19px 0;
  }
}

@media screen and (max-width: 680px) {
  .profile {
    flex-direction: column;
  }
}
